@import url(https://fonts.googleapis.com/css?family=Karla:400,700&display=swap);
*{padding:0px;margin:0px;border:0px}*,*:before,*:after{box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}aside,nav,footer,header,section{display:block}html,body{height:100%;margin:0;padding:0;position:relative;width:100%;color:#000}body{font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-weight:inherit;font-size:inherit}body{margin:0;font-family:'Karla', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body.lock{overflow:hidden}@media (max-width: 770px){body.lock{width:100%;position:fixed;overflow:hidden}}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}


.footer{padding:20px;display:flex;justify-content:space-between;align-items:center;background-color:#494949;color:#ffffff;font-size:16px;line-height:24px;flex-wrap:wrap}.footer a{color:#ffffff;-webkit-transition:all 0.1s ease 0.1s;transition:all 0.1s ease 0.1s}.footer a:hover{color:#b2bbc4}.footer__logo{max-width:120px}.footer__logo img{width:67px}.footer__terms{max-width:120px}.footer__help{margin:0 2% 0 0}.footer__subscribe{min-width:270px}.help{max-width:165px}.help__title{color:#b2bbc4;text-transform:uppercase;font-size:14px}.subscribe__title{color:#b2bbc4;font-size:20px;margin-bottom:10px}.subscribe__form{border:1px solid #b2bbc4;display:flex;justify-content:center;align-items:center;font-size:0;line-height:0}.subscribe__input{flex:1 1 auto;margin:15px 0 15px 15px}.subscribe__input input{width:100%;font-size:16px;background-color:#494949;font-family:Karla;color:#b2bbc4}.subscribe__submit{margin:auto 5px}.social{display:flex;font-size:0;margin:15px 0}.social__link{height:34px;width:34px;fill:#ffffff;margin:0 7.5px;-webkit-transition:all 0.3s ease 0.3s;transition:all 0.3s ease 0.3s}.social__link:hover{fill:#b2bbc4}

.top-bar{background-color:#F7F7F8;display:flex;justify-content:space-between;align-items:center;padding:10px 10px 10px 35px}.top-bar__right{display:flex;flex-wrap:nowrap}.top-bar__logo img{height:30px;max-width:300px}.top-bar a{color:#494949;font-size:16px;font-style:normal;margin:0 35px 0 0}.top-bar a:hover{text-decoration:underline}

.header{position:fixed;top:0;left:0;width:100%;z-index:3}

